.burger {
  position: relative;
  display: block;
  width: 44px;
  height: 20px;
}

.burger span {
  position: absolute;
  left: 0;
  top: 50%;
  width: 100%;
  height: 3px;
  background-color: #262626;
  transform: translateY(-50%);
  transition: transform 0.2s ease 0s;
}

.burger::before, .burger::after {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 3px;
  background-color: #262626;
  transition: all 0.2s ease 0s;
}

.burger::before {
  top: 0;
}

.burger::after {
  bottom: 0;
}

.burger.active span {
  transform: scale(0);
}

.burger.active:before {
  top: 9px;
  transform: rotate(45deg);
}

.burger.active:after {
  bottom: 9px;
  transform: rotate(-45deg);
}

