  left: 30px;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr 1fr 1fr;
  grid-template-columns: auto auto;
  grid-auto-flow: column;
  column-gap: 20px;
  counter-reset: number;
}

@media (max-width: 991.98px) {
  .menu__list {
    display: flex;
    flex-direction: column;
    height: 100%;
    top: auto;
    left: auto;
  }
}

.menu__item {
  width: 100%;
  display: flex;
  align-items: center;
  margin-bottom: 25px;
}

.menu__item::before {
  counter-increment: number;
  content: "0" counter(number);
  margin-right: 30px;
  font-size: 18px;
  line-height: 170%;
  color: rgba(38, 38, 38, 0.6);
  transition: color 0.3s ease;
}

@media (max-width: 767.98px) {
  .menu__item::before {
    font-size: 14px;
  }
}

@media (min-width: 767.98px) {
  .menu__item:hover::before {
    color: #000;
  }
  .menu__item:focus::before {
    color: #000;
  }
}

@media (max-width: 991.98px) {
  .menu__item {
    margin-bottom: 0;
  }
  .menu__item:not(:last-child) {
    margin-bottom: 25px;
  }
}

.menu__link {
  position: relative;
  margin-right: 80px;
  color: #262626;
  font-size: 40px;
  line-height: 110%;
}

.menu__link span {
  position: relative;
  display: inline-block;
  overflow: hidden;
}

.menu__link span::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 2px;
  background-color: #262626;
  transform: translate(-110%, -50%);
  transition: transform 0.3s ease;
}

.menu__link::after {
  content: "";
  position: absolute;
  top: 50%;
  right: -90px;
  width: 45px;
  height: 34px;
  opacity: 0;
  background: url(../img/arrow.svg) center/cover no-repeat;
  transform: translate(-20px, -50%);
  transition: transform 0.3s ease, opacity 0.3s ease;
}

@media (max-width: 991.98px) {
  .menu__link::after {
    display: none;
  }
}

@media (min-width: 767.98px) {
  .menu__link:hover::after {
    opacity: 1;
    transform: translate(0, -50%);
    transition: transform 0.3s ease, opacity 0.3s ease;
  }
  .menu__link:hover span::before {
    transform: translate(0, 0);
    transition: transform 0.3s ease;
  }
  .menu__link:focus {
    outline: none;
  }
  .menu__link:focus::after {
    opacity: 1;
    transform: translate(0, -50%);
    transition: transform 0.3s ease, opacity 0.3s ease;
  }
  .menu__link:focus span::before {
    transform: translate(0, 0);
    transition: transform 0.3s ease;
  }
}

@media (max-width: 1215px) {
  .menu__link {
    font-size: 32px;
  }
}

@media (max-width: 991.98px) {
  .menu__link {
    margin-right: 0;
  }
}

@media (max-width: 767.98px) {
  .menu__link {
    font-size: 20px;
  }
}

