/*! Footer */
.snackbar {
  position: fixed;
  bottom: 30px;
  right: 50px;
  z-index: 1;
  width: auto;
  min-width: 200px;
  padding: 15px;
  visibility: hidden;
  border-radius: 2px;
  color: #fff;
  text-align: center;
}

@media (max-width: 767.98px) {
  .snackbar {
    right: 50%;
    transform: translateX(50%);
  }
}

.snackbar.show-notification {
  visibility: visible;
  background-color: #333;
  /* Если 3000 в js, то в стилях 2.6s */
  animation: fadein 0.5s, fadeout 0.5s 2.6s;
}

.snackbar.show-attention {
  visibility: visible;
  background-color: #ff6060;
  /* Если 3000 в js, то в стилях 2.6s */
  animation: fadein 0.5s, fadeout 0.5s 2.6s;
}

@keyframes fadein {
  from {
    bottom: 0;
    opacity: 0;
  }
  to {
    bottom: 30px;
    opacity: 1;
  }
}

@keyframes fadeout {
  from {
    bottom: 30px;
    opacity: 1;
  }
  to {
    bottom: 0;
    opacity: 0;
  }
}

